* > * { box-sizing: border-box; }

body { width: 100%; overflow-x: hidden; background: #E0F7FA; font-family: "Avenir Next", sans-serif; }

.content { padding: 1.5rem 3rem; display: block; width: 70%; margin: auto; }

.caption { width: 100%; text-align: center; padding-top: 5vh; }

a { text-decoration: none; color: #000; }

nav { z-index: 1; position: absolute; bottom: 0; right: 0; padding: 0.2rem 2.5rem; color: #FFF; font-size: 0.85rem; letter-spacing: 1px; font-weight: 500; opacity: 0.5; transition: all 0.2s; }

nav:hover { opacity: 1; }

nav ul { list-style: none; }

nav ul li { display: inline-block; }

h1 { font-family: "Athelas", serif; font-size: 3.5rem; font-weight: 700; margin: 0; }

h2 { font-family: "Avenir Next", sans-serif; margin-bottom: 0.5vh; }

.subtitle { font-family: "Avenir Next", sans-serif; font-weight: 300; font-style: italic; }

label { cursor: pointer; }

button { background: none; padding: 0.8rem 1rem; border: 2px solid black; border-radius: 4px; cursor: pointer; transition: all 0.05s; user-select: none; }

button:hover { background: #000; color: white; }

button:disabled { background: none; border-color: grey; color: grey; cursor: not-allowed; }

.controls { font-size: 1.6rem; padding: 2rem 3rem; }

button.control { font-weight: 200; padding: 0.3rem 1.2rem; }

.nav { display: none; }

.chooser { width: 60%; margin: auto; text-align: center; }

.chooser a { display: inline-flex; justify-content: center; align-items: center; flex-direction: column; padding: 1rem 0.5rem; margin: 3rem 0 0.5rem; border-radius: 4px; flex-shrink: 1; width: 40%; animation: fade-in 0.2s backwards; border: 2px solid rgba(0, 0, 0, 0.4); }

.chooser a:hover { background: rgba(255, 255, 255, 0.4); }

.chooser span { text-align: center; }

.chooser img { width: 10vh; display: block; }

.story { margin-left: 12vh; transition: all 0.6s; }

.story.dock { transform: translate(10%, 5%); }

.story .storyboard { margin-top: 1.5vh; line-height: 1.5; }

.story .date { font-weight: 400; font-family: "American Typewriter", serif; }

.chooser a:nth-child(1) { animation-delay: 0.05s; }

.chooser a:nth-child(2) { animation-delay: 0.1s; }

.chooser a:nth-child(3) { animation-delay: 0.15s; }

.chooser a:nth-child(4) { animation-delay: 0.2s; }

.hide { opacity: 0; transition: all 0.2s; }

