.map { position: absolute; right: 3vw; bottom: 20vh; width: auto; height: 45vh; pointer-events: none; transition: all 0.2s; }

.path { position: absolute; right: 3vw; bottom: 20vh; height: 45vh; width: auto; pointer-events: none; }

.ship { width: auto; height: 58vh; position: absolute; left: 0; bottom: 11vh; transform-origin: 50% 0%; animation: move-in-ship 2s, wave-ship 2.5s 2s alternate infinite; pointer-events: none; }

.ship.dock { transform: translateX(-100%); animation: none; }

.wave-1, .wave-2, .wave-3 { width: 110vw; height: 12vh; background: url(../images/wave.svg) 0 100% repeat-x; background-size: contain; position: absolute; left: -4vw; bottom: -1vh; animation: wave-1 5s alternate infinite; pointer-events: none; }

.wave-1.speed, .wave-2.speed, .wave-3.speed { contain: strict; transform: translateZ(0); animation: move-wave 10s linear infinite reverse; }

.wave-2 { position: absolute; left: -4vw; bottom: 4.8vh; animation: wave-1 5s 0.3s alternate infinite; filter: hue-rotate(10deg) brightness(80%); }

.wave-3 { position: absolute; left: -4vw; bottom: 10vh; animation: wave-1 5s 0.6s alternate infinite; filter: hue-rotate(15deg) brightness(70%); }

.portrait { position: absolute; left: -20vh; bottom: 10vh; background-size: cover; height: 80vh; width: 70vh; background-position: 0 0; opacity: 0.4; pointer-events: none; }

.flag { font-size: 5rem; }

#mover { animation: pulse 0.6s alternate infinite; }
